import d3 from 'd3'

// 颜色渐变方法
function gradient (svg, color1, color2, id, direction) {
  let defs = svg.append('defs')
  let dir = direction; // 1纵向，0横向

  let gradient = defs.append('linearGradient')
    .attr('id', id)
    .attr('x1', '0%')
    .attr('y1', '0%')
    .attr('x2', function () {
      return dir ? '0%' : '100%'
    })
    .attr('y2', function () {
      return dir ? '100%' : '0%'
    })
    .attr('spreadMethod', 'pad')

  gradient.append('svg:stop')
    .attr('offset', '0%')
    .attr('stop-color', color1)
    .attr('stop-opacity', 1)

  gradient.append('svg:stop')
    .attr('offset', '100%')
    .attr('stop-color', color2)
    .attr('stop-opacity', 1)
}

// 生成渐变色方法
function gradientColor (data, colorStart, colorEnd) {
  let colorsScale = d3.scale.linear()
    .domain([0, data.length])
    .range([colorStart, colorEnd])
  return colorsScale
}
function gradientColorMore (data, arr) {
  let array = []
  let addNum = (1 / (arr.length - 1)).toFixed(2)
  for (let i = 0; i < (arr.length - 1); i++) {
    array.push(data.length * addNum * i)
  }
  array.push(data.length)
  let colorsScale = d3.scale.linear()
    .domain(array)
    .range(arr)
  // let colorsScale = d3.scale.linear()
  // .domain([0, data.length * .33, data.length * .66, data.length])
  // .range(['#ed8636', '#bf2160', '#489cea', '#8eef46'])
  return colorsScale
}

export { gradient, gradientColor, gradientColorMore }
